body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main {
  width: 500px; /* ширина */
  margin: 0 auto; /* центрування блоку */
  padding: 10px 30px;
  border: 4px solid rgb(91, 91, 209);
  border-radius: 10px;
  background-color: #d3d9f6;
}

.title {
  font-size: 24px;
  color: rgb(217, 99, 15);
}

.list {
  list-style: none;
  padding-left: 0;
  font-size: 16px;
}

.task {
  display: flex;
  justify-content: space-between; /* горизонтально - по краям */
  align-items: center; /* вертикально - по центру */
  border-bottom: 1px solid mediumpurple; /* нижня рамка пыд задачею */
}

.btn {
  padding: 10px 20px;
  margin-left: 10px;
  background-color: white;
  color: darkblue;
  border: 1px solid rgb(65, 65, 135);
  border-radius: 4px;
  font-size: 14px;

  transition: all 0.3s; /* плавна анімація */
}

.btn:hover {
  background-color: chocolate;
  color: white;
  cursor: pointer;
}

input {
  padding: 10px;
  border: 1px solid rgb(228, 30, 30);
  border-radius: 4px;
  font-size: 16px;
}

input::placeholder {
  color: rgb(242, 8, 8);
  letter-spacing: 2px;
}

.complete {
  text-decoration: line-through;
  color: grey;
}

.notcomplete {
}
